<template>
  <v-container>
    <NavbarS :name="user.name" :avatar="user.avatar" :initials="user.initials" />

    <v-alert text v-model="alert.show" :type="alert.type" dismissible>{{ alert.message }}</v-alert>
    <h1 class="font-weight-light">All plannings</h1>
    <v-row justify="center">
      <v-card class="ma-3" max-width="344" v-for="planning in planningsList" :key="planning.id_c">
        <v-img src="https://cdn.vuetifyjs.com/images/cards/sunshine.jpg" height="200px" class="white--text align-end">
          <v-card-title>{{ planning.c_name }}</v-card-title>
          <v-card-subtitle class="white--text">
            <b>Planner:</b>
            {{ planning.p_name }}
          </v-card-subtitle>
        </v-img>
        <v-card-text>
          {{ planning.c_description }}
          <div>
            <b>E-mail:</b>
            {{ planning.p_email }}
          </div>
        </v-card-text>
        <v-card-actions>
          <v-btn color="purple" dark absolute right @click="joinIn(planning.id_c)">join</v-btn>
        </v-card-actions>
      </v-card>
    </v-row>
  </v-container>
</template>

<script>
import NavbarS from "@/components/NavbarS";

export default {
  data: () => ({
    user: { role: "", name: "", email: "", avatar: "", initials: "", },
    supervisor: {},
    planningsList: [],
    alert: { show: false }
  }),
  methods: {
    async joinIn(id_c) {
      try {
        const res = await this.axios.post(
          `/supervisor/planning/${id_c}`,
          this.user
        );
        const index = this.planningsList.findIndex(c => c.id_c == id_c);
        this.planningsList.splice(index, 1);
        this.alert = {
          show: true,
          type: "success",
          message: res.data.message
        };
      } catch (error) {
        console.log(error);
      }
    }
  },
  created: async function () {
    this.user = JSON.parse(sessionStorage.getItem("session"));
    if (this.user == null) {
      this.$router.push("/");
    } else if (this.user.role != "supervisor") {
      this.$router.push("/profile");
    } else {
      try {
        console.log(this.user.id)
        const res = await this.axios.get(`/supervisor/plannings/${this.user.id}`);
        this.planningsList = res.data;
      } catch (error) {
        console.log(error);
      }
    }
  },
  components: {
    NavbarS
  }
};
</script>

