<template>
  <v-container class="text-center">
    <NavbarS :name="user.name" :avatar="user.avatar" :initials="user.initials" />
    <v-alert text v-model="alert.show" :type="alert.type" dismissible class="text-start">{{ alert.message }}</v-alert>
    <h1 class="font-weight-light">Delivery {{ planning.shift }} - {{ planning.team }}</h1>




    <v-row>
      <v-col md="9" sm="12">
        <v-card>
          <v-list-item v-for="ot in otList" :key="ot.id_a">
            <v-list-item-content class="text-left">
              <v-list-item-title>{{ ot.a_description }}</v-list-item-title>

              <v-list-item-subtitle class="text--primary">Smartcap</v-list-item-subtitle>

              <v-list-item-subtitle>{{ ot.a_name }}</v-list-item-subtitle>
            </v-list-item-content>

            <v-list-item-action>
              <v-list-item-action-text>16-05-22</v-list-item-action-text>

              <v-icon color="grey lighten-1">
                mdi-star-outline
              </v-icon>
            </v-list-item-action>

          </v-list-item>
        </v-card>
      </v-col>
    </v-row>





  </v-container>
</template>

<script>
import NavbarP from "@/components/NavbarP";
import DateTime from 'luxon/src/datetime.js'


export default {
  data: () => ({
    user: { role: "", name: "", email: "", avatar: "", initials: "" },
    date: "",
    date_finish: "",
    week: "",
    modal: false,
    alert: { show: false },
    planner: {},
    planningsList: [],
    supervisorsList: [],
    planningToAdd: {},
    planningToEdit: {},
    add: false,
    edit: false,
    team: [
      'Mina',
      'Planta',
      'Noche',
    ],
    shift: ['A', 'B'],
  }),
  methods: {
    async addPlanning() {
      let valid = this.$refs.addForm.validate();
      if (valid) {
        this.planningToAdd.date_init = DateTime.fromISO(this.date).toFormat('dd-MM-yy')
        this.planningToAdd.p_id = this.planner.id;
        this.planningToAdd.week = DateTime.fromISO(this.date).weekNumber
        this.planningToAdd.date_finish = DateTime.fromISO(this.date).plus({ days: 6 }).toFormat('dd-MM-yy')
        this.planningToAdd.s_id = 1;
        this.planningToAdd.worker = 3;
        console.log(this.planningToAdd)
        try {
          const res = await this.axios.post("/supervisor/delivery", this.planningToAdd);
          this.planningsList.push(res.data.planning);

          this.$refs.addForm.reset();
          this.add = false;
          this.alert = {
            show: true,
            type: "success",
            message: res.data.message
          };
        } catch (error) {
          console.log(error);
        }
      }
    },


    async readPlanning(id_c) {
      try {
        const res = await this.axios.get(`/planner/planning/${id_c}`);
        this.planningToEdit = res.data.planning;
        this.edit = true;
      } catch (error) {
        console.log(error);
      }
    },
    async editPlanning() {
      let valid = this.$refs.editForm.validate();
      if (valid) {
        try {
          const res = await this.axios.put(
            `/planner/planning/${this.planningToEdit.id_c}`,
            this.planningToEdit
          );
          const index = this.planningsList.findIndex(
            c => c.id_c == this.planningToEdit.id_c
          );
          this.planningsList[index] = this.planningToEdit;
          this.edit = false;
          this.alert = {
            show: true,
            type: "success",
            message: res.data.message
          };
        } catch (error) {
          console.log(error.response);
        }
      }
    },
    async deletePlanning(id_c) {
      try {
        const res = await this.axios.delete(`/planner/planning/${id_c}`);
        const index = this.planningsList.findIndex(c => c.id_c == id_c);
        this.planningsList.splice(index, 1);
        this.alert = {
          show: true,
          type: "info",
          message: res.data.message
        };
      } catch (error) {
        console.log(error);
      }
    }
  },
  created: async function () {
    this.planner = JSON.parse(sessionStorage.getItem("session"));
    if (this.planner == null) {
      this.$router.push("/");
    } else if (this.planner.role != "planner") {
      this.$router.push("/profile");
    } else {
      try {
        const res = await this.axios.post(
          "/planner/my-plannings",
          this.planner
        );
        this.planningsList = res.data

      } catch (error) {
        console.log(error);
      }
    }
  },
  components: {
    NavbarP
  }
};
</script>cript>


