<template>
  <v-container>
    <v-alert text v-model="alert.show" :type="alert.type" dismissible>{{ alert.message }}</v-alert>
    <v-row justify="center">

    </v-row>
    <v-row justify="center">

      <v-col md="6" sm="6">

        <v-card>
          <v-card-title>Iniciar sesin</v-card-title>
          <v-card-text>
            <v-form class="ma-3" @submit.prevent="signin()" ref="signinForm">

              <v-text-field label="Nombre de usuario" prepend-icon="mdi-account" v-model=user.username>
              </v-text-field>
              <v-text-field label="Password" prepend-icon="mdi-lock" type="password" :rules="passwordRules"
                v-model="user.password"></v-text-field>

              <v-btn block class="secondary" type="submit">Ingresar</v-btn>
            </v-form>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  data: () => ({
    alert: { show: false, message: "" },
    nameRules: [
      value => !!value || "Nombre de usuario es requerido",
      value =>
        (value && value.length >= 5) || "Nombre de usuario debe tener al menos 5 caracteres"
    ],
    passwordRules: [
      value => !!value || "La contrasea es requerida",
    ],
    user: {},
    suForm: true
  }),
  methods: {
    async signup() {
      let valid = this.$refs.signupForm.validate();
      if (valid) {
        try {
          const res = await this.axios.post("/signup", this.user);
          this.$refs.signupForm.reset();
          this.suForm = false;
          this.alert = {
            show: true,
            type: "success",
            message: res.data.message
          }
        }
        catch (error) {
          this.alert = {
            show: true,
            type: "error",
            message: "error.response.data.message"
          };
        }
      }
    },
    async signin() {
      let valid = this.$refs.signinForm.validate();
      if (valid) {
        try {

          const res = await this.axios.post("/signin", this.user);
          console.log(res.data)
          if (res.data.NotFound) {
            this.alert = {
              show: true,
              type: "error",
              message: res.data.message
            };
          } else {
            console.log('esta ruta')
            sessionStorage.setItem("session", JSON.stringify(res.data));
            this.$router.push('/profile')
          }
        } catch (error) {
          this.alert = {
            show: true,
            type: "error",
            message: error.response.data.message
          };
        }
      }
    }
  }
};
</script>
