<template>
<v-container>
    <p>Esta vista es para hacer pruebas aisladas, si la est viendo en pruduccin, favor avisarle a el desarrollador Gonzalo</p>
    {{porDos}}
    <v-row justify-center>
    <v-col md="12" sm="12">
    <v-card class="ma-3">
    
    <h3>Normal: {{mensaje}}</h3> 
    <br>
    <h3>Invertido:{{invertido}}</h3>
    <v-spacer></v-spacer>
    <v-text-field  v-model="mensaje"></v-text-field>
    </v-card>
    </v-col>
    </v-row>

Desarrollo de graficos de cumplimiento diario
<v-container>

    <v-row>
        <v-col>
            <h3>Cuadrilla Mina</h3>
        </v-col>
        <v-col>
            <div class="text-center">
    <v-progress-circular
      :rotate="-90"
      :size="100"
      :width="15"
      :value="calcTotal"
      color="teal"
    >
      {{ calcTotal }}
    </v-progress-circular>
            </div>
        </v-col>
    </v-row>
    
    <v-row justify="center" no-gutters>
        <v-col md="6" sm="6">
            <v-card class="pa-2" outlined tile>
            Lunes
            </v-card >
        </v-col >
        <v-col md="6" sm="6">
            <v-card class="pa-2" outlined tile>
                <v-progress-linear
                    :value="lunes"
                    color="amber"
                    height="25"
                >
                 <strong >{{ Math.ceil(lunes) }}%</strong>
                </v-progress-linear>
            </v-card> 
        </v-col > 
    </v-row>
     <v-row justify="center" no-gutters>
        <v-col md="6" sm="6">
            <v-card class="pa-2" outlined tile>
           Martes
            </v-card >
        </v-col >
        <v-col md="6" sm="6">
            <v-card class="pa-2">
                <v-progress-linear
                    :value="martes"
                    color="amber"
                    height="25"
                >
                 <strong >{{ Math.ceil(martes) }}%</strong>
                </v-progress-linear>
            </v-card> 
        </v-col > 
    </v-row>
    <v-row justify="center" no-gutters>
        <v-col md="6" sm="6">
            <v-card class="pa-2" outlined tile>
            miercoles
            </v-card >
        </v-col >
        <v-col md="6" sm="6">
            <v-card class="pa-2" outlined tile>
                <v-progress-linear
                    :value="miercoles"
                    color="amber"
                    height="25"
                >
                 <strong >{{ Math.ceil(miercoles) }}%</strong>
                </v-progress-linear>
            </v-card> 
        </v-col > 
    </v-row>
     <v-row justify="center" no-gutters>
        <v-col md="6" sm="6">
            <v-card class="pa-2" outlined tile>
           jueves
            </v-card >
        </v-col >
        <v-col md="6" sm="6">
            <v-card class="pa-2">
                <v-progress-linear
                    :value="jueves"
                    color="amber"
                    height="25"
                >
                 <strong >{{ Math.ceil(jueves) }}%</strong>
                </v-progress-linear>
            </v-card> 
        </v-col > 
    </v-row>
</v-container>
    

   

    <v-col md="12" sm="12">
    <v-card class="pa-md-6 mx-auto">
        <v-card-title>Aumento de barra</v-card-title>
        
        <v-progress-linear
      v-model="contador"
      height="25"
      color="color"
      background-color="grey"
      :class="color"
    >
    
      <strong >{{ Math.ceil(contador) }}%</strong>
    </v-progress-linear>
<v-spacer></v-spacer>
    <v-btn color="green" @click="contador=contador+5" width="50%">+</v-btn>
    <v-btn color="red" @click="contador=contador-5" width="50%">-</v-btn>
<!-- <v-text-field  v-model="fecha_f" :valor="calcFechaFinal"></v-text-field> -->
   </v-card>
    </v-col>

   

    <v-col md="12" sm="12">
    <v-card class="pa-md-6 mx-auto">
        <h2>Manejo de fechas</h2>
        <v-text-field label="Ingrese fecha de inicio" v-model="fecha_i"></v-text-field>
<h3>Fecha final: {{fecha_f}}</h3>

<h3>Semana:{{calcSemana}}</h3>
<h3>Fecha final: {{calcFechaFinal}}</h3>
<v-btn @click="fecha_final">Calcular fecha</v-btn>

         </v-card>
    </v-col>



    <h1>{{uno}}</h1>
        <v-btn @click="sumar">suma</v-btn>

        
    </v-container>
</template>
<script>
import DateTime from 'luxon/src/datetime.js'
export default {
    data:() =>({
        mensaje:"",
        contador:0,
        fecha_i:"2022-01-03",
        week:"",
        fecha_f:"",
        uno: 1,
        lunes: 20,
        martes: 35,
        miercoles: 60,
        jueves: 80,
        total: 0,
        numeros:[1,2,3],
        comida:["","",""],
        areas:["mina","planta"]


    }),
    methods:{
        
     sumar(){
      this.uno = this.uno + 1
    },
    fecha_final(){
        this.fecha_f=DateTime.fromISO(this.fecha_i).plus({days: 7}).toFormat('dd-MM-yy')
    }, 
    
    },
     computed:{
        porDos(){
            return this.numeros.map(n => n * 2)
        },
        calcTotal(){
            return (this.lunes + this.martes+this.miercoles+this.jueves)/4
        },
         calcFechaFinal(){
         return    DateTime.fromISO(this.fecha_i).plus({days: 7}).toFormat('dd-MM-yy')
         },
         calcSemana(){
             return DateTime.fromISO(this.fecha_i).weekNumber
         },
        invertido(){
            return this.mensaje.split("").reverse().join("");
        },
        color(){
            return{
                "green":this.contador <=20,
                "yellow": this.contador > 20 && this.contador < 50,
                "red": this. contador >= 50
                }

            }
        },
       
    }

</script>