<template>
  <v-container>
    <NavbarS :name="user.name" :avatar="user.avatar" :initials="user.initials" />
    <v-alert text v-model="alert.show" :type="alert.type" dismissible>{{ alert.message }}</v-alert>
    <h1 class="font-weight-light">PM03</h1>
    <!--  {{ pm03ToAdd }}
    {{ pm03List }}
 -->
    Area : {{pm03ToAdd.area}} <br>
    Sector: {{pm03ToAdd.sector}}
    <h1>Tecnicos</h1>
    {{ technicalList}}

    <v-row justify="center">
      <v-card v-for="pm03 in pm03List" :key="pm03.id" class="ma-3" max-width="344">
        <v-list-item>
          <v-list-item-content>
            <v-list-item-title>Turno {{ pm03.shift }} - Team {{ pm03.team }}</v-list-item-title>
            <v-list-item-title>Semana {{ pm03.week }}</v-list-item-title>
            <v-list-item-subtitle>
              Desde {{ pm03.star_date }}, hasta {{ pm03.date_finish }}
            </v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
        <v-list-item>
          <v-list-item-icon>
            <v-icon>mdi-account-group</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>P. Paez, L. Cifuentes, J. Velasquez</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <v-card-actions>
          <v-btn color="blue" small dark fab @click="readpm03(pm03.id)">
            <v-icon>mdi-pencil</v-icon>
          </v-btn>
          <v-btn color="red" small dark fab @click="deletepm03(pm03.id)">
            <v-icon>mdi-delete</v-icon>
          </v-btn>
          <v-spacer></v-spacer>
          <v-btn color="purple" text @click="$router.push(`/supervisor/pm03/${pm03.id}`)">ots</v-btn>
        </v-card-actions>
      </v-card>
    </v-row>

    <v-dialog v-model="add">
      <v-card>
        <v-card-title>Abrir PM03</v-card-title>
        <v-card-text>
          <v-form ref="addForm" @submit.prevent="addpm03()" class="ma-3">
            <v-row>

              <v-col cols="12" sm="6" md="4">
                <v-select label="Team" required v-model="pm03ToAdd.teamwork" :items="team"></v-select>

              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-text-field label="OT" v-model="pm03ToAdd.ot"></v-text-field>

              </v-col>


              <!--  Select de tecnicos -->

              <v-col cols="12" sm="6" md="4">
                <v-autocomplete v-model="pm03ToAdd.assigned_id" :items="technicalList" chips color="blue-grey lighten-2"
                  label="Asignado(s)" item-text="name" item-value="id" multiple>
                  <template v-slot:selection="data">
                    <v-chip v-bind="data.attrs" :input-value="data.selected" close @click="data.select"
                      @click:close="remove(data.item)">

                      <v-avatar color="secondary">
                        <span class="white--text">{{ data.item.initials }}</span>
                      </v-avatar>
                      <!-- <v-avatar left>
                        <v-img :src="data.item.initials"></v-img>
                      </v-avatar> -->
                      {{ data.item.name }}
                    </v-chip>
                  </template>
                  <template v-slot:item="data">
                    <template v-if="typeof data.item !== 'object'">
                      <v-list-item-content v-text="data.item"></v-list-item-content>
                    </template>
                    <template v-else>
                      <v-list-item-avatar>

                        <v-avatar color="secondary">
                          <span class="white--text">{{ data.item.initials }}</span>
                        </v-avatar>
                        <!-- <img :src="data.item.avatar"> -->
                      </v-list-item-avatar>
                      <v-list-item-content>
                        <v-list-item-title v-html="data.item.name"></v-list-item-title>
                        <v-list-item-subtitle>Turno {{data.item.shift}}</v-list-item-subtitle>
                      </v-list-item-content>
                    </template>
                  </template>
                </v-autocomplete>
              </v-col>


              <v-col cols="12" sm="6" md="4">
              <v-autocomplete v-model="pm03ToAdd.area" :items="areasList" chips clearable hide-details hide-selected
                :search-input.sync="search" item-text="name" item-value="id" value="id" label="Selecciona una rea."
                solo>
                <template v-slot:no-data>
                  <v-list-item>
                    <v-list-item-title>

                    </v-list-item-title>
                  </v-list-item>
                </template>


                <template v-slot:item="{ item }">
                  <v-list-item-avatar :color="item.color" class="text-h5 font-weight-light white--text">
                    {{ item.name.charAt(0) }}
                  </v-list-item-avatar>
                  <v-list-item-content>
                    <v-list-item-title v-text="item.name"></v-list-item-title>

                  </v-list-item-content>
                </template>
              </v-autocomplete>
              </v-col>

              <v-col cols="12" sm="6" md="4">
              <v-autocomplete v-model="pm03ToAdd.sector" :items="sectorsList" chips clearable hide-details hide-selected
                item-text="name" item-value="id" label="Sectores" solo :search-input.sync="search">
                <template v-slot:no-data>
                  <v-list-item>
                    <v-list-item-title>
                      Escoge un sector
                    </v-list-item-title>
                  </v-list-item>
                </template>

                <template v-slot:item="{ item }">
                  <v-list-item-avatar class="text-h5 font-weight-light white--text" :color="item.color">
                    {{ item.area.charAt(0) }}
                  </v-list-item-avatar>
                  <v-list-item-content>
                    <v-list-item-title>{{ item.name }}</v-list-item-title>
                    <v-list-item-subtitle :color="item.color">REA {{ item.area }}</v-list-item-subtitle>
                  </v-list-item-content>

                </template>
              </v-autocomplete>
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-text-field label="Servicio*" required v-model="pm03ToAdd.service"></v-text-field>
              </v-col>

              <v-col cols="12" sm="6" md="4">
                <v-text-field label="Componente*" required v-model="pm03ToAdd.component"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-text-field label="Equipo*" required v-model="pm03ToAdd.equipment"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-text-field label="Estado de equipo" v-model="pm03ToAdd.equipment_status"></v-text-field>
              </v-col>



              <v-col cols="12" sm="6" md="4">
                <v-textarea label="Descripcin" v-model="pm03ToAdd.description"></v-textarea>
              </v-col>



              <v-col cols="12" sm="6" md="4">
                <v-switch flat label="Servicio afectado" v-model="pm03ToAdd.no_service"></v-switch>
              </v-col>

              <!-- Inicio de fechas---- -->
              <v-col cols="12" sm="6" md="4">
                <v-menu ref="menu1" v-model="menu1" :close-on-content-click="false"
                  :return-value.sync="pm03ToAdd.notice_date" transition="scale-transition" offset-y min-width="auto">
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field v-model="pm03ToAdd.notice_date" label="Fecha de notificacin"
                      prepend-icon="mdi-calendar" readonly v-bind="attrs" v-on="on"></v-text-field>
                  </template>
                  <v-date-picker v-model="pm03ToAdd.notice_date" no-title scrollable>
                    <v-spacer></v-spacer>
                    <v-btn text color="primary" @click="menu1 = false">
                      Cancel
                    </v-btn>
                    <v-btn text color="primary" @click="$refs.menu1.save(pm03ToAdd.notice_date)">
                      OK
                    </v-btn>
                  </v-date-picker>
                </v-menu>
              </v-col>
              <v-btn block class="success mt-3" type="submit" @click="addpm03()">add</v-btn>
            </v-row>
          </v-form>
        </v-card-text>
      </v-card>
    </v-dialog>




    <v-btn @click=" add=true" color="secondary" large right fixed bottom fab dark>
      <v-icon>mdi-plus</v-icon>
    </v-btn>
  </v-container>
</template>

<script>
import NavbarS from "@/components/NavbarS";
import DateTime from 'luxon/src/datetime.js'


export default {
  data: () => ({
    user: {},
    menu1: false,
    pm03List: [],
    pm03ToAdd: {},
    pm03ToEdit: {},
    alert: { show: false },
    add: false,
    search: null,
    saludo: { saludo: 'me escuchas?' },
    areasList: [],
    sectorsList: [],
    technicalList : [],
    area: {},
    idArea: "",
    pm03ToAdd: {},
    team: ['Mina', 'Planta', 'Noche']
  }),
 
  watch: {
    sector(val) {
      if (val != null) this.tab = 0
      else this.tab = null
    },
    async search(val) {
      try {
        const res = await this.axios.post('/sectors', this.pm03ToAdd.area);
        console.log(res.data.sectors)
        /* this.sectorsList = res.data.sectors;
        console.log(sectorsList) */

      } catch (error) {
        console.log(error)
      } 


    },
  },
  methods: {
    //remover los tecnicos
    remove(item) {
      const index = this.pm03ToAdd.assigned_id.indexOf(item.id)
      
      if (index >= 0) this.pm03ToAdd.assigned_id.splice(index, 1)
    },
    async addpm03() {
      let valid = this.$refs.addForm.validate();
      if (valid) {
        this.pm03ToAdd.create_by_id = this.user.id;
        this.pm03ToAdd.assigned_id = [6,5];
        this.pm03ToAdd.priority = 1;
        console.log(this.pm03ToAdd)
        try {
          const res = await this.axios.post("/pm03/create", this.pm03ToAdd);
          this.pm03sList.push(res.data.pm03);

          this.$refs.addForm.reset();
          this.add = false;
          this.alert = {
            show: true,
            type: "success",
            message: res.data.message
          };
        } catch (error) {
          console.log(error);
        }
      }
    },

    async readpm03(id) {
      try {
        const res = await this.axios.get(`/supervisor/pm03/${id}`);
        this.pm03ToEdit = res.data.pm03;
        this.edit = true;
      } catch (error) {
        console.log(error);
      }
    },
    async editpm03() {
      let valid = this.$refs.editForm.validate();
      if (valid) {
        try {
          const res = await this.axios.put(
            `/supervisor/pm03/${this.pm03ToEdit.id}`,
            this.pm03ToEdit
          );
          const index = this.pm03sList.findIndex(
            c => c.id == this.pm03ToEdit.id
          );
          this.pm03sList[index] = this.pm03ToEdit;
          this.edit = false;
          this.alert = {
            show: true,
            type: "success",
            message: res.data.message
          };
        } catch (error) {
          console.log(error.response);
        }
      }
    },
    async deletepm03(id) {
      try {
        const res = await this.axios.delete(`/supervisor/pm03/${id}`);
        const index = this.pm03sList.findIndex(p => p.id == id);
        this.pm03sList.splice(index, 1);
        this.alert = {
          show: true,
          type: "info",
          message: res.data.message
        };
      } catch (error) {
        console.log(error);
      }
    },
    async getSectors(area) {
      console.log(area)
      /*  try {
         const sectores = await this.axios.post("/supervisor/sectors", this.pm03ToAdd.area);
         console.log(sectores.data.contestar)
         this.sectorsList = sectores.data.sectors.map(n => n.name);
       
 
       } catch (error) {
         console.log(error);
       } */
    }
  },

  created: async function () {
    this.user = JSON.parse(sessionStorage.getItem("session"));
    if (this.user == null) {
      this.$router.push("/");
    } else if (this.user.role != "supervisor") {
      this.$router.push("/profile");
    } else {
      try {
        const res = await this.axios.post(
          "/supervisor/my-pm03",
          this.user
        );
        this.pm03sList = res.data

      } catch (error) {
        console.log(error);
      }
    }


    //obtener datos
    try {
      const technical = await this.axios.get("/getWorkers");
      this.technicalList = technical.data.technical
      
    } catch (error) {
      console.log(error);
    }
    try {
      const areas = await this.axios.post("/areas", this.saludo);

      this.areasList = areas.data.areas;
      console.log(this.areasList)
    } catch (error) {
      console.log(error);
    }
    

  },


  components: {
    NavbarS
  }
};
</script>
