<template>
  <v-container>
    <NavbarP v-if="user.role == 'planner'" :name="user.name" :avatar="user.avatar" :initials="user.initials" />
    <NavbarS v-else :name="user.name" :avatar="user.avatar" :initials="user.initials" />

    <v-card class="mx-auto" max-width="550">
      <!--  <v-row> -->
      <v-img src=" https://cdn.vuetifyjs.com/images/cards/server-room.jpg">

        <v-card-title class="white--text mt-1">
          <v-avatar size="90">
            <v-img src="../assets/GonzaloAraya.jpg"></v-img>
          </v-avatar>
          <h1 class="ml-3">
            {{ user.name }}

          </h1>
        </v-card-title>
      </v-img>
      <!--   </v-row>
      <v-row> -->
      <v-card-text>
        <v-list-item three-line>
          <v-list-item-content>
            <v-list-item-title>{{ user.position }}</v-list-item-title>
            <v-list-item-subtitle>
              {{ user.email }}
            </v-list-item-subtitle>
            <v-list-item-subtitle>
              Turno {{ user.shift }}
            </v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
      </v-card-text>
      <!--     </v-row> -->
    </v-card>

  </v-container>
</template>

<script>
import NavbarP from "@/components/NavbarP";
import NavbarS from "@/components/NavbarS";

export default {
  data: () => ({
    user: {},
    mensaje: "hola conchatumare"
  }),
  created: function () {
    this.user = JSON.parse(sessionStorage.getItem("session"));
    console.log(this.user)
    if (this.user == null) {
      this.$router.push("/");
    }
  },
  components: {
    NavbarP,
    NavbarS
  },
  computed: {
    invertido() {
      return this.mensaje.split("").reverse().join("");
    },
    url() {
      return this.user.avatar = `../assets/${this.user.avatar}`
    }
  }
};
</script>
