<template>
  <div>
    <v-app-bar dark app dense>
      <v-app-bar-nav-icon @click.stop="drawer = !drawer"></v-app-bar-nav-icon>

      <v-toolbar-title>Ticapp {{ name }}</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-avatar color="secondary">
        <span class="white--text text-h6">{{ initials }}</span>
      </v-avatar>

      <v-btn icon>
        <v-icon>mdi-filter</v-icon>
      </v-btn>

      <v-btn icon>
        <v-icon>mdi-dots-vertical</v-icon>
      </v-btn>
    </v-app-bar>



    <v-navigation-drawer v-model="drawer" absolute bottom temporary>
      <v-list nav dense>
        <v-list-item-group v-model="group" active-class="primary">
          <v-list-item to="/supervisor/plannings">Planes semanales</v-list-item>
          <v-list-item to="/supervisor/my-plannings">Planificaciones asignadas</v-list-item>
          <v-list-item to="/supervisor/ot">OT planificadas</v-list-item>
          <v-list-item to="/supervisor/pm03">PM03</v-list-item>
          <v-list-item to="/profile">Perfil</v-list-item>
          <v-list-item @click="SignOut()">Cerrar Sesin</v-list-item>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>

  </div>
</template>

<script>
export default {
  data: () => ({
    drawer: false,
    group: null,
  }),

  watch: {
    group() {
      this.drawer = false
    },
  },
  methods: {
    SignOut() {
      sessionStorage.clear();
      this.$router.push("/");
    }
  },
  props: ['name', 'avatar', 'initials']
};
</script>
