<template>
    <v-card color="blue-grey darken-1" dark :loading="isUpdating">
        <template v-slot:progress>
            <v-progress-linear absolute color="green lighten-3" height="4" indeterminate></v-progress-linear>
        </template>
        {{assigned}}
        <v-form>
            <v-container>
                <v-row>

                    <v-col cols="12">
                        <v-autocomplete v-model="assigned" :disabled="isUpdating" :items="people" filled chips
                            color="blue-grey lighten-2" label="Select" item-text="name" item-value="id" multiple>
                            <template v-slot:selection="data">
                                <v-chip v-bind="data.attrs" :input-value="data.selected" close @click="data.select"
                                    @click:close="remove(data.item)">
                                    <v-avatar left>
                                        <v-img :src="data.item.avatar"></v-img>
                                    </v-avatar>
                                    {{ data.item.name }}
                                </v-chip>
                            </template>
                            <template v-slot:item="data">
                                <template v-if="typeof data.item !== 'object'">
                                    <v-list-item-content v-text="data.item"></v-list-item-content>
                                </template>
                                <template v-else>
                                    <v-list-item-avatar>
                                        <img :src="data.item.avatar">
                                    </v-list-item-avatar>
                                    <v-list-item-content>
                                        <v-list-item-title v-html="data.item.name"></v-list-item-title>
                                        <v-list-item-subtitle v-html="data.item.group"></v-list-item-subtitle>
                                    </v-list-item-content>
                                </template>
                            </template>
                        </v-autocomplete>
                    </v-col>
                </v-row>
            </v-container>
        </v-form>
        
    </v-card>
</template>

<script>
  export default {
    data () {
      const srcs = {
        1: 'https://cdn.vuetifyjs.com/images/lists/1.jpg',
        2: 'https://cdn.vuetifyjs.com/images/lists/2.jpg',
        3: 'https://cdn.vuetifyjs.com/images/lists/3.jpg',
        4: 'https://cdn.vuetifyjs.com/images/lists/4.jpg',
        5: 'https://cdn.vuetifyjs.com/images/lists/5.jpg',
      }

      return {
       
        assigned: [],
     
       
        people: [
          { header: 'Turno A' },
          { id:1, name: 'Sandra Adams', group: 'Group 1', avatar: srcs[1] },
          { id:2,  name: 'Ali Connors', group: 'Group 1', avatar: srcs[2] },
          { id:3,  name: 'Trevor Hansen', group: 'Group 1', avatar: srcs[3] },
          { id:4,  name: 'Tucker Smith', group: 'Group 1', avatar: srcs[2] },
          { divider: true },
          { header: 'Turno B' },
          { id:5, name: 'Britta Holt', group: 'Group 2', avatar: srcs[4] },
          { id:6, name: 'Jane Smith ', group: 'Group 2', avatar: srcs[5] },
          { id:7, name: 'John Smith', group: 'Group 2', avatar: srcs[1] },
          { id:8, name: 'Sandra Williams', group: 'Group 2', avatar: srcs[3] },
        ],
    
      }
    },

    watch: {
      isUpdating (val) {
        if (val) {
          setTimeout(() => (this.isUpdating = false), 3000)
        }
      },
    },

    methods: {
      remove (item) {
        const index = this.assigned.indexOf(item.name)
            if (index >= 0) this.assigned.splice(index, 1)
      },
    },
  }
</script>