              <v-icon v-if="complete" color="success" @click="openForm(ot.id_a)">mdi-cloud-upload</v-icon>
              <v-icon v-else color="info" @click="openForm(ot.id_a)">mdi-cloud-upload</v-icon>

            </v-list-item-action>



            <v-card-actions>
