    <script>
    import NavbarS from '@/components/NavbarS';
    
    export default {
      data: () => ({
        user: {},
        supervisor: {},
        planningsList: [],
        headers: [{
          text: 'Semana',
          align: 'start',
          sortable: true,
          value: 'week',
        },
        { text: 'Turno', value: 'shift' },
        { text: 'Team', value: 'team' },
        { text: 'Inicio', value: 'date_init' },
        { text: 'Fin', value: 'date_finish' },
        { text: 'OTs', value: 'actions', sortable: false },
        ]
      }),
      created: async function () {
        this.user = JSON.parse(sessionStorage.getItem("session"));
        if (this.user == null) {
          this.$router.push("/");
        } else if (this.user.role != "supervisor") {
          this.$router.push("/profile");
        } else {
          try {
            const res = await this.axios.post('/supervisor/my-plannings', this.user);
            this.planningsList = res.data;
    
          } catch (error) {
            console.log(error);
          }
        }
      },
      components: {
        NavbarS
      }
    };
    </script>
