<template>
    <div>
        {{ area }}
       
        {{ sector }}
        <v-autocomplete v-model="area.id" :items="areasList" :loading="isLoading" chips clearable hide-details hide-selected
            :search-input.sync="search" item-text="name" item-value="id" value="id" label="Selecciona una rea." solo>
            <template v-slot:no-data>
                <v-list-item>
                    <v-list-item-title>

                    </v-list-item-title>
                </v-list-item>
            </template>

            <template v-slot:item="{ item }">
                <v-list-item-avatar :color="item.color" class="text-h5 font-weight-light white--text">
                    {{ item.name.charAt(0) }}
                </v-list-item-avatar>
                <v-list-item-content>
                    <v-list-item-title v-text="item.name"></v-list-item-title>

                </v-list-item-content>
            </template>
        </v-autocomplete>


        <v-autocomplete v-model="sector.id" :items="sectorsList" :loading="isLoading" chips clearable hide-details
            hide-selected item-text="name" item-value="id" label="Sectores" solo :search-input.sync="search">
            <template v-slot:no-data>
                <v-list-item>
                    <v-list-item-title>
                        Escoge un sector
                    </v-list-item-title>
                </v-list-item>
            </template>

            <template v-slot:item="{ item }">
                <v-list-item-avatar class="text-h5 font-weight-light white--text" :color="item.color">
                    {{ item.area.charAt(0) }}
                </v-list-item-avatar>
                <v-list-item-content>
                    <v-list-item-title>{{ item.name }}</v-list-item-title>
                    <v-list-item-subtitle :color="item.color">REA {{ item.area }}</v-list-item-subtitle>
                </v-list-item-content>

            </template>
        </v-autocomplete>

    </div>
</template>

<script>
export default {
    data: () => ({
        isLoading: false,
        items: [],
        model: null,
        search: null,
        tab: null,
        user: {},
        area: { id: 2 },
        areasList: [],
        sectorsList: [],
        sector: {}
    }),

    watch: {
        sector(val) {
            if (val != null) this.tab = 0
            else this.tab = null
        },
        async search(val) {

            
            
            try {
                const res = await this.axios.post('/sectors', this.area);
                this.sectorsList = res.data.sectors;

            } catch (error) {
                console.log(error)
            } finally {
                this.isLoading = false
            }
           


        },
    },
    created: async function () {
        this.user = JSON.parse(sessionStorage.getItem("session"));

        if (this.user == null) {
            this.$router.push("/");
        }
        try {
            const areas = await this.axios.post("/areas", this.saludo);
            
            this.areasList = areas.data.areas;
            /* this.idArea = areas.data.areas.filter(n=n.name==this.pm03ToAdd.area) */

            console.log(this.areasList)
        } catch (error) {
            console.log(error);
        }
        

    }
}
</script>