<template>
  <v-container class="text-center">
    <NavbarP :name="user.name" :avatar="user.avatar" :initials="user.initials" />
    <v-alert class="text-start" text v-model="alert.show" :type="alert.type" dismissible>{{ alert.message }}</v-alert>

    <!-- <template> -->
    <div>
      <v-alert text v-model="alert.show" :type="alert.type" dismissible>{{ alert.message }}</v-alert>
      <v-data-table width="1000px" :headers="headers" :items="pmList" :search="search" sort-by="id_pm"
        class="elevation-10" calculate-widths fixed-header>
        <template v-slot:top>
          <v-toolbar flat>
            <v-toolbar-title>Pautas de mantencin</v-toolbar-title>
            <v-divider class="mx-4" inset vertical></v-divider>



            <v-text-field v-model="search" append-icon="mdi-magnify" label="Buscar" single-line hide-details>
            </v-text-field>



            <v-dialog v-model="dialog" max-width="500px">
              <template v-slot:activator="{ on, attrs }">
                <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on">
                  Nueva Pauta
                </v-btn>
              </template>
              <v-card>
                <v-card-title>
                  <span class="text-h5">{{ formTitle }}</span>
                </v-card-title>

                <v-card-text>
                  <v-container>
                    <v-row>
                      <v-col cols="12" sm="6" md="4">
                        <v-text-field v-model="editedItem.pm_area" label="rea"></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-text-field v-model="editedItem.pm_sector" label="Sector"></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-text-field v-model="editedItem.pm_service" label="Servicio"></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-text-field v-model="editedItem.pm_component" label="Componente"></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-text-field v-model="editedItem.pm_equipment" label="Equipo"></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-text-field v-model="editedItem.pm_description" label="Descripcin"></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-text-field v-model="editedItem.pm_workers" label="Personas"></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-text-field v-model="editedItem.pm_duration" label="Duracin"></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-text-field v-model="editedItem.pm_hh" label="HH"
                          value="`${editedItem.pm_duration*editedItem.pm_workers}`"></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-text-field v-model="editedItem.pm_frecuency" label="Frecuencia"></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-text-field v-model="editedItem.pm_days" label="Cada cuantos das"></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-text-field v-model="editedItem.pm_less" label="Menos"></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-menu ref="menu" v-model="menu" :close-on-content-click="false"
                          :return-value.sync="editedItem.pm_date" transition="scale-transition" offset-y
                          min-width="auto">
                          <template v-slot:activator="{ on, attrs }">
                            <v-text-field v-model="editedItem.pm_date" label="Desde" prepend-icon="mdi-calendar"
                              readonly v-bind="attrs" v-on="on"></v-text-field>
                          </template>
                          <v-date-picker v-model="editedItem.pm_date" no-title scrollable>
                            <v-spacer></v-spacer>
                            <v-btn text color="primary" @click="menu = false">
                              Cancel
                            </v-btn>
                            <v-btn text color="primary" @click="$refs.menu.save(editedItem.pm_date)">
                              OK
                            </v-btn>
                          </v-date-picker>
                        </v-menu>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-switch v-model="editedItem.pm_active" flat label="Activo"></v-switch>

                      </v-col>
                    </v-row>
                  </v-container>
                </v-card-text>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="close">
                    Cancelar
                  </v-btn>
                  <v-btn color="blue darken-1" text @click="save">
                    Guardar
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
            <v-dialog v-model="dialogDelete" max-width="520px">
              <v-card>
                <v-card-title class="text-h5">Estas seguro de que quieres eliminar esta Pauta de Mantencin?
                </v-card-title>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
                  <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
                  <v-spacer></v-spacer>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-toolbar>
        </template>


        <template v-slot:item.actions="{ item }">
          <v-icon small class="mr-2" @click="editItem(item)">
            mdi-pencil
          </v-icon>
          <v-icon small @click="deleteItem(item)">
            mdi-delete
          </v-icon>
        </template>


      </v-data-table>
    </div>
  </v-container>
</template>


<script>
import NavbarP from "@/components/NavbarP";
import DateTime from 'luxon/src/datetime.js';

export default {
  data: () => ({
    alert: { show: false },
    edit: false,
    menu: false,
    search: "",
    dialog: false,
    dialogDelete: false,
    headers: [
      {
        text: 'rea',
        align: 'start',
        value: 'pm_area',
      },
      { text: 'Sector', value: 'pm_sector' },
      { text: 'Servicio', value: 'pm_service', width: 100 },
      { text: 'Componente', value: 'pm_component' },
      { text: 'Equipo', value: 'pm_equipment' },
      { text: 'Descripcin', value: 'pm_description' },
      /* { text: 'Pers.', value: 'pm_workers',}, */
      /*  { text: 'Dur.', value: 'pm_duration',}, */
      { text: 'HH', value: 'pm_hh', },
      /*  { text: 'Frec.', value: 'pm_frecuency',}, */
      { text: 'Cada (Das)', value: 'pm_days' },
      /*    { text: 'Menos', value: 'pm_less',}, */
      /* { text: 'Desde', value: 'pm_date',}, */
      { text: 'Acciones', value: 'actions', sortable: false },
    ],
    pmList: [],
    newList: [],
    editedIndex: -1,
    pmToAdd: {},
    editedItem: {
      pm_area: "",
      pm_sector: "",
      pm_service: "",
      pm_component: "",
      pm_equipment: "",
      pm_description: "",
      pm_workers: 0,
      pm_duration: 0,
      pm_hh: 0,
      pm_frecuency: 0,
      pm_days: 0,
      pm_less: 0,
      pm_date: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
      pm_active: true
    },
    defaultItem: {
      pm_area: "",
      pm_sector: "",
      pm_service: "",
      pm_component: "",
      pm_equipment: "",
      pm_description: "",
      pm_workers: 0,
      pm_duration: 0,
      pm_hh: 0,
      pm_frecuency: 0,
      pm_days: 0,
      pm_less: 0,
      pm_date: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
      pm_active: true
    },
  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? 'Nueva Pauta' : 'Editar pauta'
    },
    hh() {
      return this.pmList.pm_hh = this.pmlist.pm_workers * this.pmList.pm_duration
    }


  },

  watch: {
    dialog(val) {
      val || this.close()
    },
    dialogDelete(val) {
      val || this.closeDelete()
    },
  },

  created: function () {
    /* this.initialize(), */
    this.user = JSON.parse(sessionStorage.getItem("session"));
    if (this.user == null) {
      this.$router.push("/");
    } else if (this.user.role != "planner") {
      this.$router.push("/profile");
    } else {

      // PM
      this.axios
        .get(`/planner/pms`)
        .then(res => {
          this.pmList = res.data;
          this.pmList.map(n => n.pm_date = DateTime.fromISO(n.pm_date).toFormat('yyyy-MM-dd'))



          /* console.log(res.data) */
        })
        .catch(e => {
          console.log(e);
        });
    }
  },


  methods: {

    async addPm() {
      /*  let valid = this.$refs.addForm.validate();
       if (valid) { */

      /* console.log(this.editedItem) */
      try {
        const res = await this.axios.post("/planner/pms", this.editedItem);
        this.pmList.push(res.data.pm);
        console.log(res.data.pm)


        this.alert = {
          show: true,
          type: "success",
          message: res.data.message
        };
      } catch (error) {
        /* console.log(error) */

      }
      this.close()
      /* } */
    },
    async editPm() {
      /*   let valid = this.$refs.editForm.validate();
        if (valid) { */

      try {
        const res = await this.axios.put(`/planner/pms/${this.editedItem.id_pm}`, this.editedItem);
        Object.assign(this.pmList[this.editedIndex], this.editedItem);
        this.edit = false;
        this.alert = {
          show: true,
          type: "success",
          message: res.data.message
        };
      } catch (error) {
        console.log(error);
      }
      this.close()
    },
    async deletePm() {
      const id = this.pmList[this.editedIndex].id_pm
      console.log(id)
      try {
        const res = await this.axios.delete(`/planner/pms/${id}`);
        /*    this.pmList.splice(index, 1); */
        this.alert = {
          show: true,
          type: "info",
          message: res.data.message
        };
      } catch (error) {
        console.log(error);
      }
    },

    editItem(item) {
      this.editedIndex = this.pmList.indexOf(item)
      console.log(this.editedIndex)
      this.editedItem = Object.assign({}, item)
      console.log(this.editedItem)
      this.dialog = true


    },

    deleteItem(item) {
      this.editedIndex = this.pmList.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialogDelete = true

    },

    deleteItemConfirm(item) {
      this.deletePm()
      this.pmList.splice(this.editedIndex, 1)
      this.closeDelete()
    },

    close() {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    closeDelete() {
      this.dialogDelete = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    save() {
      //editar
      if (this.editedIndex > -1) {
        this.editPm()

      } else {
        //crear
        this.addPm()
      }

    },
  },
  components: { NavbarP }
}


</script>