<template>
  <v-container>
    <NavbarP :name="user.name" :avatar="user.avatar" :initials="user.initials" />
    <v-alert text v-model="alert.show" :type="alert.type" dismissible>{{ alert.message }}</v-alert>
    <h1 class="font-weight-light">Planificaciones.</h1>

    <v-row justify="center">
      <v-card v-for="planning in planningsList" :key="planning.id_c" class="ma-3" max-width="344">
        <v-list-item>
          <v-list-item-content>
            <v-list-item-title>Turno {{ planning.shift }} - Team {{ planning.team }}</v-list-item-title>
            <v-list-item-title>Semana {{ planning.week }}</v-list-item-title>
            <v-list-item-subtitle>
              Desde {{ planning.date_init }}, hasta {{ planning.date_finish }}
            </v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
        <v-list-item>
          <v-list-item-icon>
            <v-icon>mdi-account-group</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>P. Paez, L. Cifuentes, J. Velasquez</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <v-card-actions>
          <v-btn color="blue" small dark fab @click="readPlanning(planning.id_c)">
            <v-icon>mdi-pencil</v-icon>
          </v-btn>
          <v-btn color="red" small dark fab @click="deletePlanning(planning.id_c)">
            <v-icon>mdi-delete</v-icon>
          </v-btn>
          <v-spacer></v-spacer>
          <v-btn color="purple" text @click="$router.push(`/planner/ots/${planning.id_c}`)">ots</v-btn>
        </v-card-actions>
      </v-card>
    </v-row>



    <v-dialog v-model="add" max-width="450">
      <v-card>
        <v-card-title>Crear plan semanal</v-card-title>
        <v-card-text>
          <v-form ref="addForm" @submit.prevent="addPlanning()" class="ma-3">
            <!-- <v-text-field
              prepend-icon="mdi-biohazard"
              label="Name"
              :rules="[(v) => !!v || 'fecha inicio is required']"
              v-model="planningToAdd.c_name"
            ></v-text-field>
            <v-text-field
              prepend-icon="mdi-biohazard"
              label=""
              :rules="[(v) => !!v || 'Name is required']"
              v-model="planningToAdd.c_name"
            ></v-text-field>
            <v-textarea
              prepend-icon="mdi-bike"
              label="Description"
              :rules="[(v) => !!v || 'Description is required']"
              v-model="planningToAdd.c_description"
            ></v-textarea> -->

            <!--    <v-text-field
         v-model="planningToAdd.week"
          label="Semana"
        >
        </v-text-field> -->
            <v-select v-model="planningToAdd.team" :items="team" label="Team"></v-select>

            <v-select v-model="planningToAdd.shift" :items="shift" label="Turno"></v-select>
            <!--     <v-text-field
      v-model="planningToAdd.worker"
      label="Personas"
    ></v-text-field> -->


            <!-- 
           <v-text-field
      v-model="planningToAdd.s_id"
      label="Supervisor"
    ></v-text-field> -->
            <!-- ahore empieza lo bueno -->

            <v-row>
              <v-col cols="12" sm="6" md="4">
                <v-dialog ref="dialog" v-model="modal" :return-value.sync="date" persistent width="290px">
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field v-model="date" label="Picker in dialog" prepend-icon="mdi-calendar" readonly
                      v-bind="attrs" v-on="on"></v-text-field>
                  </template>
                  <v-date-picker v-model="date" scrollable>
                    <v-spacer></v-spacer>
                    <v-btn text color="primary" @click="modal = false">
                      Cancel
                    </v-btn>
                    <v-btn text color="primary" @click="$refs.dialog.save(date)">
                      OK
                    </v-btn>
                  </v-date-picker>
                </v-dialog>
              </v-col>
              <v-spacer></v-spacer>
            </v-row>


            <!-- ahore termina lo bueno -->



            <v-btn block class="success mt-3" type="submit">add</v-btn>
          </v-form>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-dialog v-model="edit" max-width="450">
      <v-card>
        <v-card-title>Update a planning</v-card-title>
        <v-card-text>
          <v-form ref="editForm" @submit.prevent="editPlanning()" class="ma-3">
            <v-text-field prepend-icon="mdi-biohazard" label="Name" :rules="[(v) => !!v || 'Name is required']"
              v-model="planningToEdit.c_name"></v-text-field>
            <v-textarea prepend-icon="mdi-bike" label="Description" :rules="[(v) => !!v || 'Description is required']"
              v-model="planningToEdit.c_description"></v-textarea>
            <v-btn block class="info mt-3" type="submit">update</v-btn>
          </v-form>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-btn @click="add = true" color="red" large right fixed bottom fab dark>
      <v-icon>mdi-plus</v-icon>

    </v-btn>
  </v-container>
</template>

<script>
import NavbarP from "@/components/NavbarP";
import DateTime from 'luxon/src/datetime.js'


export default {
  data: () => ({
    user: { role: "", name: "", email: "", avatar: "", initials: "", },
    date: "",
    date_finish: "",
    week: "",
    modal: false,
    alert: { show: false },
    planner: {},
    planningsList: [],
    supervisorsList: [],
    planningToAdd: {},
    planningToEdit: {},
    add: false,
    edit: false,
    team: [
      'Mina',
      'Planta',
      'Noche',
    ],
    shift: ['A', 'B'],
  }),
  methods: {
    async addPlanning() {
      let valid = this.$refs.addForm.validate();
      if (valid) {
        this.planningToAdd.date_init = DateTime.fromISO(this.date).toFormat('dd-MM-yy')
        this.planningToAdd.p_id = this.user.id;
        this.planningToAdd.week = DateTime.fromISO(this.date).weekNumber
        this.planningToAdd.date_finish = DateTime.fromISO(this.date).plus({ days: 6 }).toFormat('dd-MM-yy')
        this.planningToAdd.s_id = 4;
        this.planningToAdd.worker = 3;
        console.log(this.planningToAdd)
        try {
          const res = await this.axios.post("/planner/planning", this.planningToAdd);
          this.planningsList.push(res.data.planning);

          this.$refs.addForm.reset();
          this.add = false;
          this.alert = {
            show: true,
            type: "success",
            message: res.data.message
          };
        } catch (error) {
          console.log(error);
        }
      }
    },


    async readPlanning(id_c) {
      try {
        const res = await this.axios.get(`/planner/planning/${id_c}`);
        this.planningToEdit = res.data.planning;
        this.edit = true;
      } catch (error) {
        console.log(error);
      }
    },
    async editPlanning() {
      let valid = this.$refs.editForm.validate();
      if (valid) {
        try {
          const res = await this.axios.put(
            `/planner/planning/${this.planningToEdit.id_c}`,
            this.planningToEdit
          );
          const index = this.planningsList.findIndex(
            c => c.id_c == this.planningToEdit.id_c
          );
          this.planningsList[index] = this.planningToEdit;
          this.edit = false;
          this.alert = {
            show: true,
            type: "success",
            message: res.data.message
          };
        } catch (error) {
          console.log(error.response);
        }
      }
    },
    async deletePlanning(id_c) {
      try {
        const res = await this.axios.delete(`/planner/planning/${id_c}`);
        const index = this.planningsList.findIndex(c => c.id_c == id_c);
        this.planningsList.splice(index, 1);
        this.alert = {
          show: true,
          type: "info",
          message: res.data.message
        };
      } catch (error) {
        console.log(error);
      }
    }
  },
  created: async function () {
    this.user = JSON.parse(sessionStorage.getItem("session"));
    if (this.user == null) {
      this.$router.push("/");
    } else if (this.user.role != "planner") {
      this.$router.push("/profile");
    } else {
      try {
        const res = await this.axios.post(
          "/planner/my-plannings",
          this.user
        );
        this.planningsList = res.data

      } catch (error) {
        console.log(error);
      }
    }
  },
  components: {
    NavbarP
  }
};
</script>
