<template>
  <v-container class="text-center">
    <NavbarS :name="user.name" :avatar="user.avatar" :initials="user.initials" />
    <v-alert text v-model="alert.show" :type="alert.type" dismissible class="text-start">{{ alert.message }}</v-alert>
    <!--   <h3 class="font-weight-light">Planificacin Turno {{planning.shift}} - {{planning.team}}</h3>
    <h3 class="font-weight-thin">Semana {{planning.week}} desde {{planning.date_init}} hasta {{planning.date_finish}} </h3> 
 -->

    <v-row color="green">
      <v-col md="9" sm="12">
        <v-card elevation="5" class="my-1" v-for="ot in otList" :key="ot.id_a">
          <v-list-item>
            <v-list-item-content class="text-left">
              <v-list-item-title>{{ ot.a_sector }}</v-list-item-title>
              <v-list-item-subtitle>{{ ot.a_equipment }}</v-list-item-subtitle>

              <v-list-item-subtitle class="text--primary">{{ ot.a_service }}</v-list-item-subtitle>

              <v-list-item-subtitle class="amber--text">{{ ot.a_area }}</v-list-item-subtitle>
              <v-list-item-subtitle>OT: {{ ot.id_a }}</v-list-item-subtitle>
            </v-list-item-content>
            <!-- fecha -->
            <v-list-item-action>
              <v-list-item-action-text icon="icon-calendar-range">{{ ot.a_scheduled_date }}</v-list-item-action-text>

              <v-icon v-if="complete" color="success" @click="openForm(ot.id_a)">mdi-cloud-upload</v-icon>
              <v-icon v-else color="info" @click="openForm(ot.id_a)">mdi-cloud-upload</v-icon>

            </v-list-item-action>

          </v-list-item>
        </v-card>
      </v-col>
    </v-row>

    <v-dialog v-model="add" elevation="15" overlay-opacity="0.7" transition="dialog-bottom-transition">

      <v-card>
        <v-btn icon dark @click="add = false">
          <v-icon color="secondary" darken>mdi-close</v-icon>
        </v-btn>
        <v-card-title>Entregar OT {{ otCurrent.id_a }}</v-card-title>
        <v-card-text>
          <v-row>
            <v-col cols="1">
              <v-icon color="secondary">mdi-map-marker</v-icon>
            </v-col>
            <v-col cols="11">{{ otCurrent.a_area }} - {{ otCurrent.a_sector }}</v-col>

            <v-col cols="1">
              <v-icon color="secondary">mdi-wrench-clock</v-icon>
            </v-col>
            <v-col cols="11">{{ otCurrent.a_component }} - {{ otCurrent.a_equipment }}</v-col>

            <v-col cols="1">
              <v-icon color="secondary">mdi-access-point</v-icon>
            </v-col>
            <v-col cols="11">{{ otCurrent.a_service }}</v-col>

            <v-col cols="1">
              <v-icon color="secondary">mdi-account-group</v-icon>
            </v-col>
            <v-col cols="3">{{ otCurrent.a_workers }}</v-col>
            <v-col cols="1">
              <v-icon color="secondary">mdi-timer-sand-complete</v-icon>
            </v-col>
            <v-col cols="3">{{ otCurrent.a_duration }}</v-col>
            <v-col cols="1">
              <v-icon color="secondary">mdi-clipboard-text-clock</v-icon>
            </v-col>
            <v-col cols="3">{{ otCurrent.a_type }}</v-col>
          </v-row>

          <v-form ref="addForm" class="ma-3" @submit.prevent="addDelivery">
            <!--  <v-text-field label="Hora de inicio" prepend-icon="mdi-clock-time-four-outline"
              v-model="deliveryToAdd.d_start_time"></v-text-field> -->
            <!-- <v-text-field label="Hora de termino" v-model="deliveryToAdd.d_end_time"
              prepend-icon="mdi-clock-time-eight"></v-text-field> -->

            <v-row>
              <v-col cols="6">
                <v-dialog ref="dialog" v-model="modal2" :return-value.sync="start_time" persistent width="290px">
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field v-model="start_time" label="Hora inicio" prepend-icon="mdi-clock-time-four-outline"
                      readonly v-bind="attrs" v-on="on"></v-text-field>
                  </template>
                  <v-time-picker v-if="modal2" v-model="start_time" full-width>
                    <v-spacer></v-spacer>
                    <v-btn text color="primary" @click="modal2 = false">
                      Cancel
                    </v-btn>
                    <v-btn text color="primary" @click="$refs.dialog.save(start_time)">
                      OK
                    </v-btn>
                  </v-time-picker>
                </v-dialog>
              </v-col>
              <!-- Aqui comienza el reloj de tiempo final---- -->
              <v-col cols="6">
                <v-dialog ref="dialog3" v-model="modal3" :return-value.sync="end_time" persistent width="290px">
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field v-model="end_time" label="Hora termino" prepend-icon="mdi-clock-time-four-outline"
                      readonly v-bind="attrs" v-on="on"></v-text-field>
                  </template>
                  <v-time-picker v-if="modal3" v-model="end_time" full-width>
                    <v-spacer></v-spacer>
                    <v-btn text color="primary" @click="modal3 = false">
                      Cancel
                    </v-btn>
                    <v-btn text color="primary" @click="$refs.dialog3.save(end_time)">
                      OK
                    </v-btn>
                  </v-time-picker>
                </v-dialog>
              </v-col>
            </v-row>

            <v-textarea label="Comentario" v-model="deliveryToAdd.d_comment" prepend-icon="mdi-comment-text">
            </v-textarea>

            <v-btn block class="success mt-3" type="submit">Entregar</v-btn>
          </v-form>
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import NavbarS from "@/components/NavbarS";

export default {
  data: () => ({
    user: {},
    otCurrent: "",
    d_start_time: "",
    supervisor: {},
    planning: {},
    otList: [],
    deliveryToAdd: {},
    deliveryList: [],
    alert: { show: false },
    add: false,
    uno: 1,
    modal2: false,
    modal3: false,
    start_time: "",
    end_time: "",
    complete: false
  }),
  methods: {

    /* open(link) {
      window.open(link);
    }, */
    openForm(id_a) {
      this.otCurrent = this.otList.filter(n => n.id_a == id_a)[0];
      this.deliveryToAdd.id_a = id_a;
      this.add = true;
    },
    async addDelivery() {
      let valid = this.$refs.addForm.validate();
      if (valid) {
        this.deliveryToAdd.s_id = this.supervisor.id_s,
          this.deliveryToAdd.p_id = this.planning.p_id,
          this.deliveryToAdd.id_c = this.planning.id_c,
          this.deliveryToAdd.d_start_time = this.start_time,
          this.deliveryToAdd.d_end_time = this.end_time,

          console.log(this.deliveryToAdd)
        try {
          const res = await this.axios.post("/supervisor/delivery", this.deliveryToAdd);
          console.log(this.deliveryToAdd)
          this.deliveryList.push(res.data.delivery);
          this.$refs.addForm.reset();
          this.add = false;
          this.complete = true;
          this.alert = {
            show: true,
            type: "success",
            message: res.data.message
          };
        } catch (error) {
          console.log(error);
        }
      }
    },
  },
  components: {
    NavbarS
  },
  created: async function () {

    this.user = JSON.parse(sessionStorage.getItem("session"));
    if (this.user == null) {
      this.$router.push("/");
    } else if (this.user.role != "supervisor") {
      this.$router.push("/profile");
    } else {
      try {
        const res = await this.axios.get(
          `/supervisor/ot/${this.$route.params.id_c}/${this.user.id}`
        );
        this.planning = res.data.planning;
        this.otList = res.data.ots;
      } catch (error) {
        console.log(error);
      }
    }
  }
};
</script>


