<template>
  <v-container class="text-center">
    <NavbarP :name="user.name" :avatar="user.avatar" :initials="user.initials" />
    <v-alert class="text-start" text v-model="alert.show" :type="alert.type" dismissible>{{ alert.message }}</v-alert>
    <h1 class="font-weight-light">Turno {{ planning.shift }} - {{ planning.team }}</h1>
    <h2 class="font-weight-thin">Semana {{ planning.week }}</h2>

    <v-card>
      <v-row class="text-center mx-1">
        <v-col class="mx-1">Miercoles</v-col>
        <v-col class="mx-1">Jueves</v-col>
        <v-col class="mx-1">Viernes</v-col>
        <v-col class="mx-1">Sabado</v-col>
        <v-col class="mx-1">Domingo</v-col>
        <v-col class="mx-1">Lunes</v-col>
        <v-col class="mx-1">Martes</v-col>
      </v-row>
      <v-row class="text-center mx-1">
        <v-col class="mx-1">1</v-col>
        <v-col class="mx-1">2
          <!-- {{ DateTime.fromISO(planning.date_init).plus({ days: 1 }) -->
        </v-col>
        <v-col class="mx-1">7</v-col>
        <v-col class="mx-1">8</v-col>
        <v-col class="mx-1">9</v-col>
        <v-col class="mx-1">10</v-col>
        <v-col class="mx-1">11</v-col>
      </v-row>
      <v-row class="text-center mx-1">
        <v-col class="mx-1">
          <v-progress-linear :value="miercoles" color="amber" height="20">
            <strong>{{ Math.ceil(miercoles) }}%</strong>
          </v-progress-linear>
        </v-col>
        <v-col class="mx-1">
          <v-progress-linear :value="jueves" color="amber" height="20">
            <strong>{{ Math.ceil(jueves) }}%</strong>
          </v-progress-linear>
        </v-col>
        <v-col class="mx-1">
          <v-progress-linear :value="viernes" color="amber" height="20">
            <strong>{{ Math.ceil(viernes) }}%</strong>
          </v-progress-linear>
        </v-col>
        <v-col class="mx-1">
          <v-progress-linear :value="sabado" color="amber" height="20">
            <strong>{{ Math.ceil(sabado) }}%</strong>
          </v-progress-linear>
        </v-col>
        <v-col class="mx-1">
          <v-progress-linear :value="domingo" color="amber" height="20">
            <strong>{{ Math.ceil(domingo) }}%</strong>
          </v-progress-linear>
        </v-col>
        <v-col class=" mx-1">
          <v-progress-linear :value="lunes" color="amber" height="20">
            <strong>{{ Math.ceil(lunes) }}%</strong>
          </v-progress-linear>
        </v-col>
        <v-col class="mx-1">
          <v-progress-linear :value="martes" color="amber" height="20">
            <strong>{{ Math.ceil(martes) }}%</strong>
          </v-progress-linear>
        </v-col>

      </v-row>
    </v-card>
    <!-- 
    <v-row justify="center">
      <v-col md="9" sm="12">
        <v-simple-table fixed-header>
          <thead>
            <tr>
              <th class="text-center">OT</th>
              <th class="text-center">rea</th>
              <th class="text-center">Equipo</th>
              <th class="text-center">Servicio</th>
              <th class="text-center">Fecha</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="ot in otsList" :key="ot.id_a">
              <td>{{ ot.id_a }}</td>
              <td>{{ ot.a_area }}</td>
              <td>{{ ot.a_equipment }}</td>
              <td>{{ ot.a_service }}</td>
              <td>{{ ot.a_scheduled_date }}</td>

              <td>
                <v-icon color="cyan darken-1" @click="getDeliveries(ot.id_a)">mdi-information</v-icon>
              </td>
            </tr>
          </tbody>
        </v-simple-table>
      </v-col>
    </v-row> -->

    <!-- {{ areas }}
    {{ pmList.map(pm => pm.pm_equipment) }} -->
    <!--  <tr v-for="description in pmList.filter(n => n.pm_area == area).map(pm => pm.pm_description)"> -->
    <template>
      <v-expansion-panels>
        <v-expansion-panel v-for="area in areas">
          <v-expansion-panel-header>
            {{ area }}
          </v-expansion-panel-header>
          <v-expansion-panel-content>
            <v-row>
              <v-col cols="12">
                <v-simple-table fixed-header>
                  <thead>
                    <tr>
                      <th class="text-center">Add</th>
                      <th class="text-center">SECTOR</th>
                      <th class="text-center">SERVICIO</th>
                      <th class="text-center">COMPONENTE</th>
                      <th class="text-center">EQUIPO</th>
                      <th class="text-center">FECHA</th>
                      <th class="text-center">PERSONAS</th>
                      <th class="text-center">DURACIN</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="pm in pmList.filter(n => n.pm_area == area)">

                      <td>
                        <v-checkbox hide-details></v-checkbox>
                      </td>
                      <td class="text-left">{{ pm.pm_sector }}</td>
                      <td class="text-left">{{ pm.pm_service }}</td>
                      <td class="text-left">{{ pm.pm_component }}</td>
                      <td class="text-left">{{ pm.pm_equipment }}</td>
                      <td class="text-left">27-06-22</td>
                      <td class="text-left">{{ pm.pm_workers }}</td>
                      <td class="text-left">{{ pm.pm_duration }}</td>


                      <td>
                        <v-icon color="cyan darken-1" @click="getDeliveries(ot.id_a)">mdi-information</v-icon>
                      </td>
                    </tr>
                  </tbody>
                </v-simple-table>
              </v-col>
            </v-row>



            <!-- <template>
              <v-expansion-panels>
                <v-expansion-panel
                  v-for="description in pmList.filter(n => n.pm_area == area).map(pm => pm.pm_description)">
                  <v-expansion-panel-header>
                    {{ description }}
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>

                  </v-expansion-panel-content>
                </v-expansion-panel>
              </v-expansion-panels>
            </template> -->
          </v-expansion-panel-content>
        </v-expansion-panel>
      </v-expansion-panels>
    </template>





    <v-dialog v-model="showDelv" max-width="450">
      <v-simple-table fixed-header>
        <thead>
          <tr>
            <th class="text-center">Name student</th>
            <th class="text-center">Delivery</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="delivery in deliveriesList" :key="delivery.id_d" class="text-center">
            <td>{{ delivery.s_name }}</td>
            <td>
              <a @click="open(delivery.d_file)">{{ delivery.d_filename }}</a>
            </td>
          </tr>
        </tbody>
      </v-simple-table>
    </v-dialog>

    <v-dialog v-model="add" max-width="450">
      <v-card>
        <v-card-title>Crea una OT</v-card-title>
        <v-card-text>
          <v-form ref="addForm" class="ma-3" @submit.prevent="addOt()">

            <v-select v-model="otToAdd.a_area" :items="areas" label="reas"></v-select>
            <v-select v-model="otToAdd.a_equipment" :items="equipment" label="Equipo"></v-select>
            <v-select v-model="otToAdd.a_service" :items="services" label="Servicio"></v-select>

            <v-btn block class="success mt-3" type="submit">add</v-btn>
          </v-form>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-btn @click="add = true" color="green" large right fixed bottom fab dark>
      <v-icon>mdi-plus</v-icon>
    </v-btn>
  </v-container>
</template>

<script>
import NavbarP from "@/components/NavbarP";
import DateTime from 'luxon/src/datetime.js'

export default {
  data: () => ({
    items: [
      { mensaje: 'Foo' },
      { mensaje: 'Bar' }
    ],
    alert: { show: false },
    planner: {},
    planning: {},
    otsList: [],
    otToAdd: {},
    deliveriesList: [],
    showDelv: false,
    add: false,
    pmList: [],
    areas: [],
    equipment: [
    ],
    services: [
      "CCTV Procesos",
      "800XA",
      "Radiocomunicaciones",
      "Profibus"
    ],
    miercoles: 10,
    jueves: 5,
    viernes: 20,
    sabado: 20,
    domingo: 78,
    lunes: 40,
    martes: 35,



  }),
  methods: {
    open(link) {
      window.open(link);
    },
    async addOt() {
      let valid = this.$refs.addForm.validate();
      if (valid) {
        this.otToAdd.s_id = this.planning.s_id,
          this.otToAdd.p_id = this.planning.p_id,
          this.otToAdd.id_c = this.planning.id_c,

          console.log(this.otToAdd)
        try {
          const res = await this.axios.post(`/planner/ot/${this.$route.params.id_c}`, this.otToAdd);
          this.otsList.push(res.data.ot);
          this.$refs.addForm.reset();
          this.add = false;
          this.alert = {
            show: true,
            type: "success",
            message: res.data.message
          };
        } catch (error) {
          console.log(error);
        }
      }
    },
    async getDeliveries(id_a) {
      try {
        const res = await this.axios.get(`/planner/deliveries/${id_a}`);
        this.deliveriesList = res.data;
        this.showDelv = true;
      } catch (error) {
        console.log(error);
      }
    }
  },

  created: function () {
    this.planner = JSON.parse(sessionStorage.getItem("session"));
    if (this.planner == null) {
      this.$router.push("/");
    } else if (this.planner.role != "planner") {
      this.$router.push("/profile");
    } else {
      // Planning information
      this.axios
        .get(`/planner/planning/${this.$route.params.id_c}`)
        .then(res => {
          this.planning = res.data.planning;
        })
        .catch(e => {
          console.log(e);
        });

      // Ots
      this.axios
        .get(`/planner/planning-ots/${this.$route.params.id_c}`)
        .then(res => {
          this.otsList = res.data;

        })
        .catch(e => {
          console.log(e);
        });
      this.axios
        .get(`/planner/planning-pm`)
        .then(res => {
          this.pmList = res.data;
          console.log(this.pmList)
          const areas = this.pmList.map(pm => pm.pm_area)
          this.areas = [...new Set(areas)]
          const equipos = this.pmList.map(pm => pm.pm_equipment)
          this.equipment = [...new Set(equipos)];
        })
        .catch(e => {
          console.log(e);
        });

    }


  },

  components: {
    NavbarP
  }
};
</script>

